.add-form {
    background-color: #abe;
    border-radius: 12px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px;
    gap: 20px;
}

.input-group {
    display: flex;
    align-items: center;
    gap: 20px;
}

.form-label {
    font-size: 16px;
    font-weight: 700;
}

.add-input {
    height: 40px;
    width: 240px;
    border: none;
    border-radius: 12px;
    padding: 0 12px;
}


.add-button {
    border: none;
    height: 40px;
    border-radius: 10px;
    background-color: saddlebrown;
    width: 140px;
    color: #fff;
    font-weight: 700;
}